    const getItemTotal = (anItem) => {
        //check if all of required field are filled
        const isAllFilled = !isEmpty(anItem.price) && !isEmpty(anItem.quantity);
        if (isAllFilled) {
            //calculate discount amt
            let discountAmt = 0;
            if (anItem.discount) {
                for (let aDiscount of anItem.discountsArr) {
                    if (aDiscount.type === 'fixed') {
                        discountAmt += aDiscount.rate;
                    } else {
                        discountAmt += anItem.price * aDiscount.rate * 0.01;
                    }
                }
            }
            //calculate tax amt
            let taxAmt = 0;
            if (anItem.tax) {
                for (let aTax of anItem.taxesArr) {
                    if (aTax.type === 'fixed') {
                        taxAmt += aTax.rate;
                    } else {
                        taxAmt += (anItem.price - discountAmt) * aTax.rate * 0.01;
                    }
                }
            }
            //calculate items total cost [discount-total, tax-total, item-total]
            return [discountAmt * anItem.quantity, taxAmt * anItem.quantity, anItem.quantity * (anItem.price + taxAmt - discountAmt)]
        } else return [0, 0, 0];
    }


