import { baseRequest } from '../base';
// api for Invoices 

const getInvoiceItems = async (data, signal) => {
    try {
      return await baseRequest('api/v1/sales/invoiceitems/get','POST', data, signal);
    } catch(e){
      alert(e)
    }
  };
  
  
  const deleteInvoiceItems = async (data, signal) => {
    try {
      return await baseRequest('api/v1/sales/invoiceitems/delete','POST', data, signal);
    } catch(e){
      alert(e)
    }
  };
  
  const createInvoiceItem = async (data, signal) => {
      try {
        return await baseRequest('api/v1/sales/invoiceitem/add','POST', data, signal);
      } catch(e){
        alert(e)
      }
  };
  
  
  const getInvoiceItem = async (data, signal) => {
    try {
      return await baseRequest('api/v1/sales/invoiceitem/get','POST', data, signal);
    } catch(e){
      alert(e)
    }
  };
  
  const updateInvoiceItem= async (data, signal) => {
    try {
      return await baseRequest('api/v1/sales/invoiceitems/update','POST', data, signal);
    } catch(e){
      alert(e)
    }
  };
  
export {getInvoiceItems, createInvoiceItem, deleteInvoiceItems, updateInvoiceItem, getInvoiceItem}
  