import React from 'react';
import { Modal } from 'react-bootstrap';

import Create from './Create'



const MainModal = (props) => {
    return <Create {...props} />
}

const CreateModal = ({ setCreatedToSelected, ...rest }) => {
    return <Modal
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
        {...rest}

    >
        <MainModal
            {...rest}
            setCreatedToSelected={setCreatedToSelected}
        />
    </Modal>

}

export default CreateModal;
