import isEmpty from "./isEmpty"


const validateInvoiceCreation = (invoiceData) => {
    const { customer_id, invoicedOn, dueOn, extraDiscount, totalWeight, invoiceStatus } = invoiceData;



    const errors = {}
    if (isEmpty(customer_id)) {
        errors.customer_id = 'Customer should not be empty!'
    }
    if (isEmpty(invoicedOn ? invoicedOn.toISOString() : null)) {
        errors.invoicedOn = 'This field can\'t be empty!'
    }
    if (isEmpty(dueOn ? dueOn.toISOString() : null)) {
        errors.dueOn = 'This field can\'t be empty!'
    }
    if (isEmpty(totalWeight)) {
        errors.totalWeight = 'Specify the total weight of invoice!'
    }

    // if (isEmpty(invoiceStatus)) {
    //     errors.invoiceStatus = 'Specify status of the invoice!'
    // }
    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}


