const validateInvoiceItemsCreation = (invoiceItems) => {

    console.log("here is the invoice Items", invoiceItems)

    const errors = [];
    let i = 0;
    for (let anItem of invoiceItems) {
        console.log(anItem);
        const {
            item,
            purchaseItem,
            place,
            quantity,
            price,
            discount,
            discountsArr,
            tax,
            taxesArr,
        } = anItem;
        if (isEmpty(item)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].item = 'Can\'t be empty!'
        }
        if (isEmpty(place)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].place = 'Can\'t be empty!'
        }
        if (isEmpty(purchaseItem)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].purchaseItem = 'Can\'t be empty!'
        }
        if (isEmpty(quantity)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].quantity = 'Can\'t be empty!'
        }
        if (isEmpty(price)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].price = 'Can\'t be empty!'
        }


        i++;
    }
    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }

}


